@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/ptsans.woff2") format("woff2"), url("/fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("/fonts/ptsansbold.woff2") format("woff2"), url("/fonts/ptsansbold.woff") format("woff");
}
body {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  width: 320px;
  text-align: center;
  margin: 0 auto;
  background-color: #e7e5e5;
  min-height: 100%;
}
.page__container {
  background-color: #ffffff;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  body {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  body {
    width: 1200px;
  }
}
.hero {
  position: relative;
  background-color: #eaeaea;
  background-image: url("/img/mobile/hero-background-mobile.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: bottom, bottom;
  padding-top: 89px;
  padding-bottom: 80px;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("/img/mobile/hero-background-retina-mobile.jpg");
    background-size: cover;
  }
}
.hero::after {
  content: "";
  position: absolute;
  background-image: url("/img/zip.svg");
  background-position: center bottom;
  background-size: cover;
  width: 100%;
  height: 57px;
  bottom: 0;
  left: 0;
}
@media (min-width: 768px) {
  .hero {
    background-image: url("/img/tablet/hero-background-tablet.jpg");
    background-position: bottom, 50%;
    padding-top: 77px;
    padding-bottom: 86px;
  }
  .hero__index {
    width: 461px;
    height: 350px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("/img/tablet/hero-background-retina-tablet.jpg");
    background-size: cover;
  }
}
@media (min-width: 1200px) {
  .hero {
    background-image: url("/img/desktop/hero-background-desktop.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("/img/desktop/hero-background-retina-desktop.jpg");
  }
}
.hero-catalog {
