    margin-right: 100px;
  }
  .introduce::after {
    margin-right: -53px;
  }
}
.check {
  position: relative;
}
@media (min-width: 768px) {
  .check--radio {
    padding: 0 96px 51px 96px;
  }
  .check--radio::before {
    display: block;
    position: absolute;
    content: "";
    height: 1px;
    background-color: #d6d6d6;
    width: 123px;
    left: 58px;
    top: -43px;
  }
  .check--radio::after {
    display: block;
    position: absolute;
    content: "";
    height: 1px;
    background-color: #d6d6d6;
    width: 123px;
    top: -43px;
    right: 58px;
  }
  .check--checkbox {
    padding: 0 144px 43px 144px;
  }
  .check--checkbox::before {
    display: block;
    position: absolute;
    content: "";
    height: 1px;
    background-color: #d6d6d6;
    width: 123px;
    left: 58px;
    top: -55px;
  }
  .check--checkbox::after {
    display: block;
    position: absolute;
    content: "";
    height: 1px;
    background-color: #d6d6d6;
    width: 123px;
    top: -55px;
    right: 58px;
  }
}
@media (min-width: 1200px) {
  .check {
    padding: 21px 0 75px 5px;
  }
  .check::before {
    width: 280px;
    left: 120px;
    top: -38px;
  }
  .check::after {
    width: 280px;
    right: 120px;
    top: -38px;
  }
  .check--checkbox::before {
    left: 120px;
    top: -52px;
  }
  .check--checkbox::after {
    top: -52px;
    right: 120px;
  }
}
@media (max-width: 767px) {
  .check {
    margin-bottom: 50px;
  }
  .check::after {
    display: block;
    content: "";
    height: 1px;
    background-color: #d6d6d6;
    width: 320px;
    margin-left: -20px;
    position: absolute;
    margin-top: 24px;
  }
}
