.input-item {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding-bottom: 17px;
  position: relative;
  min-height: 46px;
  display: flex;
  align-items: center;
}

.input-item:last-child {
  padding-bottom: 0;
}

.input-item__control {
  margin: 0;
}

.input-item__control:hover:not([disabled]) + .control-mark {
  background-color: @line;
}

.input-item__control:focus:not([disabled]) + .control-mark {
  background-color: @line;
}

.input-item__control:active:not([disabled]) + .control-mark {
  background-color: #d5d5d5;
}

.input-item__control:active + .control-mark--radio {
  background-image: url("/img/radio-mark-active.svg");
}

.input-item__control:active + .control-mark--checkbox {
  background-image: url("/img/check-mark-active.svg");
}

.input-item__control:disabled + .control-mark {
  opacity: 0.7;
}

.input-item__text {
  cursor: pointer;
  padding-left: 62px;
  display: flex;
  align-items: center;
  height: 100%;
  z-index: 1;
}

.input-item__control:checked + .control-mark--radio {
  background-image: url("/img/radio-mark.svg");
}

.input-item__control:checked + .control-mark--checkbox {
  background-image: url("/img/check-mark.svg");
}

@media (min-width: @tablet-width) {
  .input-item {
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .input-item:nth-child(3) {
    margin-right: 10px;
  }

  .input-item:nth-child(4) {
    margin-right: 17px;
  }
}
